<template>
  <div class="content">
    <div class="container">
      <img :src="detail.sound.pic_500" alt class="cover" />
      <div class="progress-bar" @touchstart="handleSeek" ref="progressBar">
        <div>{{audioObject.currentTime|formatTime}}/{{audioObject.duration|formatTime}}</div>
        <div class="progressing" :style="`width:${getProgress}`"></div>
      </div>
      <div class="song-info-container">
        <div class="play-icon" :class="audioObject.isPlay?'pause':'play'" @click="controlPlay"></div>
        <div class="info">
          <div class="info-top">{{detail.sound.name}}</div>
          <div class="info-bottom">
            <span class="user-name">{{detail.sound.user.name}}</span>
            <div class="song-channel">
              发布在
              <span class="channel-name">{{detail.sound.channel.name}}</span>频道
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="play-amount">
      <div class="left">
        <span>{{detail.sound.view_count}} 播放</span>
        <span class="like">{{detail.sound.like_count}} 喜欢</span>
      </div>
      <div class="right">设为手机铃声</div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
export default {
  name: "Content",
  data() {
    return {};
  },
  computed: {
    ...mapState(["detail", "audioObject"]),
    ...mapGetters(["getProgress"])
  },
  methods: {
    ...mapMutations(["setPlayStatus", "setCurrentTime"]),
    controlPlay() {
      this.setPlayStatus();
    },
    // 快进操作
    //这里是按比例来计算:当前时间/总时长=点击的位置/总宽度
    //当点击快进完成修改音乐的时长的时候会触发音乐的oncanplay事件
    handleSeek(e) {
      // 新版touch
      this.$refs.progressBar.ontouchmove = (e) => {
        let seekWidth =
          (e.touches[0].pageX * this.audioObject.duration) /
          document.documentElement.clientWidth;
        this.setCurrentTime(seekWidth);
        this.audioObject.ele.currentTime = seekWidth;
        this.setPlayStatus(true);
      };
      //原版click
      // let seekWidth =
      //   (epageX * this.audioObject.duration) /
      //   document.documentElement.clientWidth;
      // this.setCurrentTime(seekWidth);
      // this.audioObject.ele.currentTime = seekWidth;
      // this.setPlayStatus(true);
    }
  }
};
</script>

<style lang="less" scoped>
.content {
  width: 100%;
  height: auto;
  background-color: #fff;

  .container {
    width: 100%;
    height: 375px;
    position: relative;
    .cover {
      width: 100%;
      height: 100%;
    }
    .progress-bar {
      position: absolute;
      width: 100%;
      height: 15px;
      left: 0;
      bottom: 60px;
      background-color: rgba(0, 0, 0, 0.2);
      font-size: 13px;
      line-height: 15px;
      color: white;
      .progressing {
        width: 0;
        background: rgba(110, 213, 108, 0.4);
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    .song-info-container {
      position: absolute;
      width: 100%;
      height: 60px;
      left: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.5);
      padding: 10px;
      display: flex;
      align-items: center;
      font-size: 15px;
      box-sizing: border-box;
      .play-icon {
        width: 38px;
        height: 38px;
        // background: url("~@/assets/img/play.png") no-repeat;
        background-size: 100% 100%;
        &.play {
          background: url("~@/assets/img/pause.png") no-repeat;
          background-size: 100% 100%;
        }
        &.pause {
          background: url("~@/assets/img/play.png") no-repeat;
          background-size: 100% 100%;
        }
      }
      .info {
        color: @white;
        margin-left: 18px;
        height: 100%;
        .info-top {
          font-size: 14px;
        }
        .info-bottom {
          display: flex;
          font-size: 13px;
          align-items: center;
          height: 15px;
          .user-name {
            margin-right: 2px;
            height: 100%;
            color: @shallowGreen;
          }
          .song-channel {
            height: 100%;
            display: flex;
            .channel-name {
              display: block;
              max-width: 127px;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              color: @shallowGreen;
              margin: 0 2px;
            }
          }
        }
      }
    }
  }
  .play-amount {
    height: 50px;
    display: flex;
    align-items: center;
    font-size: 13px;
    color: #999;
    padding: 15px;
    box-sizing: border-box;
    justify-content: space-between;
    border-bottom: 1px solid #f4f4f4;
    .left {
      .like {
        margin-left: 10px;
      }
    }
    .right {
      color: @shallowGreen;
    }
  }
}
</style>