<template>
  <van-loading type="spinner" v-if="!detailData" vertical></van-loading>
  <div class="detail" v-else>
    <Header></Header>
    <Content></Content>
    <Bottom></Bottom>
    <relativeList :listData="otherList" banner="相关推荐" ref="list"></relativeList>
  </div>
</template>

<script>
import Header from "./header";
import Content from "./content";
import Bottom from "./bottom";
import relativeList from "../../components/list";
import { getDetail, getOtherList } from "../../axios/api";
import { mapMutations, mapState } from "vuex";
export default {
  name: "Detail",
  data() {
    return {
      otherList: []
    };
  },
  mounted() {
    this.getDetailData();
    this.getOtherList();
  },
  components: {
    Header,
    Content,
    Bottom,
    relativeList
  },
  computed: {
    ...mapState(["id", "detail", "audioObject"]),
    detailData() {
      return Object.keys(this.detail).length;
    }
  },
  methods: {
    ...mapMutations([
      "saveDetail",
      "saveDetailId",
      "setPlayStatus",
      "setPlayList"
    ]),
    // 获取详情相关的数据
    async getDetailData() {
      let detail;
      // 当没有id或者是传递的id值不一样时再发请求,这也是解决在首页点击列表的某一项遇到重复的问题
      if (!this.id || this.id !== this.$route.query.id) {
        this.saveDetailId(this.$route.query.id);
        detail = await getDetail(this.id);
        this.saveDetail(detail);
        // this.setPlayStatus(true);
        this.setPlayList(detail);
        return;
      }
    },
    //获取相关推荐
    async getOtherList() {
      this.otherList = await getOtherList();
      this.$nextTick(() => {
        if (this.$refs.list) {
          this.$refs.list.loading = false;
          this.$refs.list.finished = true;
        }
      });
    }
  },
  watch: {}
};
</script>

<style scoped>
.detail {
}
</style>